header {
    height: 100px;
    display: flex;
    justify-content: flex-end;
}

.menu {
    margin-right: 60px;
    list-style-type: none;
    font-weight: 500;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100px;

    .menu_item {
        margin-right: 30px;
        font-family: 'Open Sans', sans-serif;
        font-weight: 600;
        font-size: 15px;
        
        a {
            color: $black;
            text-decoration: none;
        }
    }
}

.header-contacts {
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100px;

    .telefon {
        display: flex;
        justify-content: flex-end;
        font-weight: 700;
        font-size: 20px;
    }

    .icons {
        display: flex;
        justify-content: center;
        margin-top: 20px;

        .icons__item {
            margin-right: 15px;
            list-style-type: none;

            img {
                width: 30px;
                height: 30px;
            }
        }
    }
}