html {
  font-size: 16px; }

body {
  font-family: 'Roboto', 'Arial', sans-serif;
  overflow-wrap: break-word;
  word-wrap: break-word; }

.container {
  min-width: 320px;
  max-width: 100%;
  padding: 0 0;
  box-sizing: border-box;
  margin: 0 auto;
  background: #FFFB00; }

.wrap {
  min-width: 320px;
  max-width: 1200px;
  padding: 0 15px;
  box-sizing: border-box;
  margin: 0 auto; }

* {
  margin: 0;
  padding: 0; }

header {
  height: 100px;
  display: flex;
  justify-content: flex-end; }

.menu {
  margin-right: 60px;
  list-style-type: none;
  font-weight: 500;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100px; }
  .menu .menu_item {
    margin-right: 30px;
    font-family: 'Open Sans', sans-serif;
    font-weight: 600;
    font-size: 15px; }
    .menu .menu_item a {
      color: #000000;
      text-decoration: none; }

.header-contacts {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100px; }
  .header-contacts .telefon {
    display: flex;
    justify-content: flex-end;
    font-weight: 700;
    font-size: 20px; }
  .header-contacts .icons {
    display: flex;
    justify-content: center;
    margin-top: 20px; }
    .header-contacts .icons .icons__item {
      margin-right: 15px;
      list-style-type: none; }
      .header-contacts .icons .icons__item img {
        width: 30px;
        height: 30px; }

.presentation-block {
  height: 900px; }
  .presentation-block .big-text {
    margin-top: 180px;
    font-size: 150px;
    font-weight: 700; }
  .presentation-block .small-text {
    margin-top: 230px;
    font-size: 50px;
    font-weight: 500;
    text-align: end;
    font-family: 'Open Sans', sans-serif;
    font-style: italic; }
    .presentation-block .small-text span {
      margin-right: 20px; }

.content-block {
  height: 700px;
  font-size: 40px;
  font-family: 'Lobster', cursive; }
  .content-block .content-menu {
    height: 500px;
    display: flex;
    flex-direction: column; }
    .content-block .content-menu .content-menu__item {
      list-style-type: none; }
      .content-block .content-menu .content-menu__item:first-child {
        display: flex;
        flex-direction: column;
        justify-content: center; }
      .content-block .content-menu .content-menu__item:last-child {
        margin-top: 90px; }
    .content-block .content-menu .inner-menu .inner-menu__item {
      margin-top: 20px;
      font-size: 20px;
      list-style-type: none; }
      .content-block .content-menu .inner-menu .inner-menu__item:before {
        content: '⇒'; }
  .content-block .truck {
    position: absolute;
    right: 0;
    margin-right: 70px; }

.footer-menu {
  height: 100px;
  font-family: 'Roboto', 'Arial', sans-serif;
  font-weight: 700;
  font-size: 20px;
  display: flex;
  justify-content: space-between; }
  .footer-menu .telefon-number {
    order: 2; }
  .footer-menu .address {
    order: 1; }
