.content-block {
    height: 700px;
    font-size: 40px;
    font-family: 'Lobster', cursive;

    .content-menu {
        height: 500px;
        display: flex;
        flex-direction: column;

        .content-menu__item {
            list-style-type: none;

            &:first-child {
                display: flex;
                flex-direction: column;
                justify-content: center;
            }

            &:last-child {
                margin-top: 90px;
            }
        }

        .inner-menu {
            

            .inner-menu__item {
                margin-top: 20px;
                font-size: 20px;
                list-style-type: none;      

                &:before {
                    content: '⇒';
                }
            }
        }
    }

    .truck {
        position: absolute;
        right: 0;
        margin-right: 70px;
    }
}